import * as React from 'react';
import Modal from 'react-responsive-modal';
import { View, Text, TouchableHighlight, Image } from '../../../common/elements';
import { isNN } from '../../../../util/index';
import { styles as pageStyles } from '../../../../styles/page';
import { IVisitor } from '../../../../models/home';
import Toast from '../../../../util/toast';
import { FILE_BASE_PATH } from '../../../../constants';
import { toDateString } from '../../../../util/convert';

interface IProps {
  homeId: string,
  onToggleLoading: (isLoading: boolean) => void,
  onLoad: (homeId: string, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) => void
};

interface IState{
  data: Array<IVisitor>,
  isZoomImage: boolean,
  zoomImage: string
};

export default class Visitors extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      data: [],
      isZoomImage: false,
      zoomImage: ''
    }
    this.onToggleZoomImage = this.onToggleZoomImage.bind(this);
  };

  componentDidMount(){
    if(this.props.homeId.length > 0){
      this.props.onToggleLoading(true);
      this.props.onLoad(this.props.homeId, (result: Array<IVisitor>) => {
        this.props.onToggleLoading(false);
        this.setState({
          data: result
        });
      }, (message: string) => {
        this.props.onToggleLoading(false);
        Toast.fail(isNN(message) ? message : 'Failed to load Visitors.');
      });
    }
  };

  componentWillUnmount() {}

  onToggleZoomImage = (isZoom: boolean) => {
    this.setState({
      isZoomImage: isZoom
    });
  };

  render() {
    let { data } = this.state;
    if(isNN(data)){
      return(
        <View>
          {
            data.map((d: IVisitor, i: number) => {
            return(
              <View key={i} style={pageStyles.row}>
                <View style={pageStyles.ele}>
                  <Text style={{fontWeight: 'bold'}}>{d.createdBy + ' on ' + toDateString(d.createdAt)}</Text>
                </View>
                {isNN(d.name) && 
                <View style={pageStyles.ele}>
                <Text>{'[' + d._homeId + '] ' +  d.name}</Text>
                </View>
                }
                {(d.age > 0 || isNN(d.gender)) && 
                <View style={pageStyles.ele}>
                  <Text>{d.age + ', ' + (d.gender === 'M' ? 'Male' : 'Female')}</Text>
                </View>
                }
                {isNN(d.mobileNo) && 
                <View style={pageStyles.ele}>
                  <Text>{d.mobileNo}</Text>
                </View>
                }
                {isNN(d.image) &&
                <View style={pageStyles.ele}>
                  <TouchableHighlight className='pointer' onClick={() => this.setState({isZoomImage: true, zoomImage: d.image})}>
                    <Image src={FILE_BASE_PATH + d.image} style={{height: 100}} />
                  </TouchableHighlight>
                </View>
                }
              </View>
              );
          })
        }
        <Modal
          open={this.state.isZoomImage}
          onClose={() => this.onToggleZoomImage(false)}>
          <View style={{alignSelf: 'center', alignContent: 'center'}}>
            <Image src={FILE_BASE_PATH + this.state.zoomImage} style={{height: '40%'}} />
          </View>
        </Modal>
        </View>
      );
    }else{
      return(
      <Text>No data found!!!</Text>
      );
    }
  }
};