import * as React from 'react';
import Modal from 'react-responsive-modal';
import { confirmAlert } from 'react-confirm-alert';
import { TouchableHighlight, View, Image, Text } from '../../common/elements';
import Spinner from '../../common/spinner';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { IVisitor } from '../../../models/home/visitor';
import { styles as pageStyles } from "../../../styles/page";
import { isNN } from '../../../util/index';
import { FILE_BASE_PATH, ROLE } from '../../../constants';
import Button from '../../common/button';
import Toast from  '../../../util/toast';
import { IHomes } from '../../../models/store/homes';
import { toDateString } from '../../../util/convert';

interface IProps {
  user: IUser,
  appUser: IAppUser,
  homes: IHomes,
  onLoad: (appClientId: string, homeId: string, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) => void,
  onExit: (id: string, user: IUser, callback: (result: boolean) => void) => void
};

interface IState{
  data: Array<IVisitor>,
  isLoading: boolean,
  isZoomImage: boolean,
  zoomImage: string
};

export default class Visitors extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      data: [],
      isLoading: false,
      isZoomImage: false,
      zoomImage: '',
    }
    this.onExit = this.onExit.bind(this);
    this.onToggleZoomImage = this.onToggleZoomImage.bind(this);
  };

  componentDidMount(){
    this.setState({
      isLoading: true
    });
    this.props.onLoad(this.props.user.client._id, '', (result: Array<IVisitor>) => {
      this.setState({
        isLoading: false,
        data: result
      });
    }, (message: string) => {
      this.setState({
        isLoading: false
      });
      Toast.fail(message);
    });
  };
  
  componentWillUnmount(){};

  onToggleZoomImage = (isZoom: boolean) => {
    this.setState({
      isZoomImage: isZoom
    });
  };

  getHomeNumber = (homeId: string) => {
    if(this.props.homes && this.props.homes.data && this.props.homes.data.length > 0){
      let home = this.props.homes.data.filter((d, i) => d._id === homeId);
      if(home && home.length > 0){
        return home[0].number;
      }else{
        return '';
      }
    }else{
      return '';
    }
  };
  
  onExit = (id: string) => {
    confirmAlert({
      title: 'Confirm to Send Out',
      message: 'Are you sure you wish to Send Out?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            this.setState({
              isLoading: true
            });
            this.props.onExit(id, this.props.user, (result: boolean) => {
              this.setState({
                isLoading: false,
              });
              if(result === true){
                this.setState({
                  data: this.state.data.filter(v => v._id !== id)
                });
                Toast.success('Visitor sent out successfully.');
              }else{
                Toast.fail('Failed to send out visitor.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  render() {
    return(
      <main style={pageStyles.content}>
      <View style={pageStyles.row}>
        <Text>{'No of Active Visitors: ' + this.state.data.length}</Text>
      </View>
        {isNN(this.state.data) ?
        this.state.data.map((d: IVisitor, i: number) => {
          return(
            <View key={i} style={pageStyles.row}>
              <View style={pageStyles.ele}>
                <Text style={{fontWeight: 'bold'}}>{d.createdBy + ' on ' + toDateString(d.createdAt)}</Text>
              </View>
              {isNN(d.name) && 
              <View style={pageStyles.ele}>
                <Text>{'[' + d._homeId + '] ' +  d.name}</Text>
              </View>
              }
              {(d.age > 0 || isNN(d.gender)) && 
              <View style={pageStyles.ele}>
                <Text>{(d.age > 0 ? d.age : '') + ((d.age > 0 && isNN(d.gender)) ? ', ' : '') + (isNN(d.gender) ? (d.gender === 'M' ? 'Male' : 'Female') : '')}</Text>
              </View>
              }
              {isNN(d.mobileNo) && 
              <View style={pageStyles.ele}>
                <Text>{d.mobileNo}</Text>
              </View>
              }
              {isNN(d.image) &&
              <View style={pageStyles.ele}>
                <TouchableHighlight className='pointer'
                onClick={() => this.setState({isZoomImage: true, zoomImage: d.image})}>
                  <Image src={FILE_BASE_PATH + d.image} style={{height: 100}} />
                </TouchableHighlight>
              </View>
              }
              {this.props.appUser.role === ROLE.GATEKEEPER &&
              <View style={pageStyles.ele}>
                <Button title='Exit' onPress={() => { this.onExit(d._id) }} />
              </View>
              }
            </View>
          )
        })
        :
        <Text style={{padding: 20}}>No Visitor found!!!</Text>
      }
        <Modal
          open={this.state.isZoomImage}
          onClose={() => this.onToggleZoomImage(false)}>
          <View style={{alignSelf: 'center', alignContent: 'center'}}>
            <Image src={FILE_BASE_PATH + this.state.zoomImage} style={{ height: '40%'}} />
          </View>
        </Modal>
        <Spinner visible={this.state.isLoading} />
      </main>
    );
  }
};