import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { IUser } from '../models/user/user';
import { IVisitor } from '../models/home';
import { loadAll, exit } from '../store/actions/visitor';

import Page from '../components/home/gatekeeper/visitors';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    user: state.User,
    appUser: state.AppUser,
    homes: state.Homes
});
const mapDispatchToProps = (dispatch: any) => ({
    onLoad: (appClientId: string, homeId: string, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) =>  dispatch(loadAll(appClientId, homeId, sCallback, fCallback)),
    onExit: (id: string, user: IUser, callback: (result: boolean) => void) => dispatch(exit(id, user, callback))
});

export const AllVisitors = connect(mapStateToProps, mapDispatchToProps)(Page);