import * as React from 'react';
import Spinner from '../../common/spinner';
import { IHomes } from '../../../models/store/homes';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { IVisitor } from '../../../models/home';
import NewVisitor from './new-visitor';

interface IProps {
  history: any,
  appUser: IAppUser,
  user: IUser,
  homes: IHomes,
  onSaveVisitor: (homeId: string, data: IVisitor, user: IUser, appUser: IAppUser, scallback: (result: boolean) => void) => void,
  onUpload: (data: any, callback: (result: boolean, message: string) => void) => void
};

interface IState{
  isLoading: boolean
};

export default class Home extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isLoading: false
    }
  };

  componentWillUnmount() {};
  
  render() {
    return(
      <main style={styles.content}>
        <NewVisitor history={this.props.history} appUser={this.props.appUser} user={this.props.user} homes={this.props.homes} onToggleLoading={(isLoading) => this.setState({ isLoading: isLoading })} onSave={this.props.onSaveVisitor} onUpload={this.props.onUpload} />
        <Spinner visible={this.state.isLoading} />
      </main>
    );
  }
};

const styles = {
  content: {
    flex: 1,
    // backgroundColor: 'transparent',
    width: '100%'
  }
};
