import * as React from 'react';
import Modal from 'react-responsive-modal';
import { confirmAlert } from 'react-confirm-alert';
import { TouchableHighlight, View, Image, Text, TextInput } from '../common/elements';
import Spinner from '../common/spinner';
import RadioButton from '../common/radio-button';
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user/user';
import { IVisitor } from '../../models/home/visitor';
import { styles as pageStyles } from "../../styles/page";
import { styles as iconStyles } from "../../styles/icon";
import { styles as modelStyles } from "../../styles/model";
import { styles as CommonStyles} from '../../styles/common';
import Images from '../images';
import { isNN } from '../../util/index';
import { FILE_BASE_PATH } from '../../constants';
import Toast from  '../../util/toast';
import { toDateString } from '../../util/convert';

interface IProps {
  user: IUser,
  appUser: IAppUser,
  onLoad: (appClientId: string, homeId: string, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) =>  void,
  onSave: (homeId: string, data: IVisitor, user: IUser, appUser: IAppUser, callback: (result: boolean) => void) => void,
  onRemove: (id: string, user: IUser, homeIndex: number, scallback: (result: boolean) => void) => void
};

interface IState{
  data: Array<IVisitor>,
  isLoading: boolean,
  isZoomImage: boolean,
  zoomImage: string,
  isEdit: boolean,
  id: string,
  name: string,
  age: number,
  gender: string,
  mobileNo: string
};

export default class Visitors extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      data: [],
      isLoading: false,
      isZoomImage: false,
      zoomImage: '',
      isEdit: false,
      id: '',
      name: '',
      age: 0,
      gender: '',
      mobileNo: ''
    }
    this.onSave = this.onSave.bind(this);
    this.onRemove = this.onRemove.bind(this);
    this.onToggleZoomImage = this.onToggleZoomImage.bind(this);
  };

  componentWillUnmount() {};
  
  componentDidMount(){
    this.setState({
      isLoading: true
    });
    this.props.onLoad(this.props.user.client._id, this.props.user.homes[this.props.appUser.homeIndex]._id, (result: Array<IVisitor>) => {
      this.setState({
        isLoading: false,
        data: result
      });
    }, (message: string) => {
      this.setState({
        isLoading: false
      });
      Toast.fail(message);
    });
  };

  onToggleZoomImage = (isZoom: boolean) => {
    this.setState({
      isZoomImage: isZoom
    });
  };

  onSave = () => {
    if(isNN(this.state.name) && this.state.age > 0 && isNN(this.state.gender)){
        this.setState({
          isLoading: true
        });
        /*
        let data = {
          id: this.state.id,
          homeId: this.props.user.homes[this.props.appUser.homeIndex]._id,
          name: this.state.name,
          age: this.state.age,
          gender: this.state.gender,
          mobileNo: this.state.mobileNo,
          userId: this.props.user._id,
        }
        */
        let visitor = {
          _id: this.state.id,
          name: this.state.name,
          age: this.state.age,
          gender: this.state.gender,
          mobileNo: this.state.mobileNo,
          emailId: '',
          image: 'img.jpg',
        } as IVisitor;
        this.props.onSave(this.props.user.homes[this.props.appUser.homeIndex]._id, visitor, this.props.user, this.props.appUser, (result: boolean) => {          
          this.setState({
            isLoading: false,
          });
          if(result === true){
            this.setState({
              isEdit: false,
              id: '',
              name: '',
              age: 0,
              gender: '',
              mobileNo: ''
            });
            Toast.success('Visitor saved successfully.');
          }else{
            Toast.fail('Failed to save visitor.');
          }
        });
    }else{
      Toast.warn('Please fill all the required fields before '+ (this.state.id.length > 0 ? 'Update' : 'Save') + '.');
    }
  };
  
  onRemove = (id: string) => {
    confirmAlert({
      title: 'Confirm to delete',
      message: 'Are you sure you wish to delete?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            this.setState({
              isLoading: true
            });
            this.props.onRemove(id, this.props.user, this.props.appUser.homeIndex, (result: boolean) => {
              this.setState({
                isLoading: false,
              });
              if(result === true){
                Toast.success('Visitor removed successfully.');
              }else{
                Toast.fail('Failed to remove visitor.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  render() {
    return(
      <main style={pageStyles.content}>
        {isNN(this.state.data) ?
        this.state.data.map((d: IVisitor, i: number) => {
          return(
            (this.state.isEdit === true && this.state.id === d._id) ?
            <View key={i} style={pageStyles.row}> 
              <View style={modelStyles.ele}>
              <TextInput type='text' placeholder="Name" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.name} onChange={(event: any) => this.setState({name: event.target.value})} />
              </View>              
              <View style={pageStyles.ele}>
                <TextInput type='text' placeholder="Age" className='form-control' maxLength={2} style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.age > 0 ? this.state.age + '' : ''} onChange={(event: any) => this.setState({age: +(event.target.value.replace(/[^0-9]/g, ''))})} />
              </View>              
              <View style={pageStyles.ele}>
                <RadioButton isActive={this.state.gender === 'M'} text='Male' onPress={() => this.setState({gender: 'M'})} />
                <RadioButton isActive={this.state.gender === 'F'} text='Female' onPress={() => this.setState({gender: 'F'})} />
              </View>
              <View style={pageStyles.ele}>
                <TextInput type='text' placeholder="Mobile Number" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.mobileNo} onChange={(event: any) => this.setState({mobileNo: event.target.value})} />
              </View>
              <View style={pageStyles.ele}>
                <TouchableHighlight style={{marginRight: 25}} onClick={this.onSave}>
                  <Image src={Images.tick} style={iconStyles.editIcon} />
                </TouchableHighlight>                
                <TouchableHighlight style={{}} onClick={() => {
                  this.setState({
                    isEdit: false,
                    id: '',
                    name: '',
                    age: 0,
                    gender: '',
                    mobileNo: ''
                  });
                }}>
                  <Image src={Images.cancel} style={iconStyles.deleteIcon} />
                </TouchableHighlight>      
              </View>
            </View>
            :
            <View key={i} style={pageStyles.row}>
              <View style={pageStyles.ele}>
                <Text style={{fontWeight: 'bold'}}>{d.createdBy + ' on ' + toDateString(d.createdAt)}</Text>
              </View>
              {isNN(d.name) && 
              <View style={pageStyles.ele}>
                <Text>{'[' + d._homeId + '] ' +  d.name}</Text>
              </View>
              }
              {(d.age > 0 || isNN(d.gender)) && 
              <View style={pageStyles.ele}>
                <Text>{d.age + ', ' + (d.gender === 'M' ? 'Male' : 'Female')}</Text>
              </View>
              }
              {isNN(d.mobileNo) && 
              <View style={pageStyles.ele}>
                <Text>{d.mobileNo}</Text>
              </View>
              }
              {isNN(d.image) &&
              <View style={pageStyles.ele}>
                <TouchableHighlight className='pointer' onClick={() => this.setState({isZoomImage: true, zoomImage: d.image})}>
                  <Image src={FILE_BASE_PATH + d.image} style={{height: 100}} />
                </TouchableHighlight>
              </View>
              }
              {/* <View style={pageStyles.ele}>
                <TouchableHighlight underlayColor={COLOR.ICON_BTN_UNDERLAY} style={{marginRight: 25}} onPress={() => {
                  this.setState({
                    isEdit: true,
                    id: d.id,
                    name: d.name,
                    age: d.age,
                    gender: d.gender,
                    mobileNo: d.mobileNo
                  });
                }}>
                  <Image source={Images.edit} style={iconStyles.editIcon} />
                </TouchableHighlight>
                <TouchableHighlight underlayColor={COLOR.ICON_BTN_UNDERLAY} style={{}} onPress={() => {
                  this.onRemove(d.id);
                }}>
                  <Image source={Images.delete} style={iconStyles.deleteIcon} />
                </TouchableHighlight>      
              </View> */}
            </View>
          )
        })
        :
        <Text style={{padding: 20}}>No Visitor found!!!</Text>
      }
        <Modal
          open={this.state.isZoomImage}
          onClose={() => this.onToggleZoomImage(false)}>
          <View style={{alignSelf: 'center', alignContent: 'center'}}>
            <Image src={FILE_BASE_PATH + this.state.zoomImage} style={{height: '40%'}} />
          </View>
        </Modal>
        <Spinner visible={this.state.isLoading} />
      </main>
    );
  }
};