import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { IAppUser } from '../models/app-user';
import { IUser } from '../models/user/user';
import { loadAll, save, remove } from '../store/actions/visitor';

import Page from '../components/home/visitors';
import { IVisitor } from '../models/home';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    user: state.User,
    appUser: state.AppUser
});
const mapDispatchToProps = (dispatch: any) => ({
    onLoad: (appClientId: string, homeId: string, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) =>  dispatch(loadAll(appClientId, homeId, sCallback, fCallback)),
    onSave: (homeId: string, data: IVisitor, user: IUser, appUser: IAppUser, callback: (result: boolean) => void) => dispatch(save(homeId, data, user, appUser, callback)),
    onRemove: (id: string, user: IUser, homeIndex: number, scallback: (result: boolean) => void) => dispatch(remove(id, user, homeIndex, scallback))
});

export const Visitors = connect(mapStateToProps, mapDispatchToProps)(Page);